import styled from "styled-components";
import { useEffect, useRef } from "react";
import { gsap } from "gsap";
import { Button } from "../Routes/Store";
import { ScrollTrigger, Draggable } from "gsap/all";

const Foodimg = styled.div`
  position: relative;
  overflow: hidden;
  margin: auto;
  height: 70%;
  width: 80%;
  ul {
    position: absolute;
    width: 35%;
    height: 100%;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    li {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center center;
    }
  }
`;

function FoodPic() {
  const Rbtn = useRef();
  const Lbtn = useRef();
  gsap.registerPlugin(ScrollTrigger, Draggable);
  const imageLink = [
    "taco.png",
    "breakfast-burrito.png",
    "burritos.png",
    "churros.png",
    "enchiladas.png",
    "mexican-rice.png",
    "nachos.png",
    "salad-bowl.png",
    "tamales.png",
  ];

  useEffect(() => {
    let iteration = 0;
    gsap.set(".cards li", { xPercent: 400, opacity: 0, scale: 0 });
    const spacing = 0.11,
      snapTime = gsap.utils.snap(spacing),
      cards = gsap.utils.toArray(".cards li"),
      animateFunc = (element) => {
        const tl = gsap.timeline();
        tl.fromTo(
          element,
          { scale: 0, opacity: 0 },
          {
            scale: 1,
            opacity: 1,
            zIndex: 100,
            duration: 0.5,
            yoyo: true,
            repeat: 1,
            ease: "power1.in",
            immediateRender: false,
          }
        ).fromTo(
          element,
          { xPercent: 400 },
          { xPercent: -400, duration: 1, ease: "none", immediateRender: false },
          0
        );
        return tl;
      },
      seamlessLoop = buildSeamlessLoop(cards, spacing, animateFunc),
      playhead = { offset: 0 },
      wrapTime = gsap.utils.wrap(0, seamlessLoop.duration()),
      scrub = gsap.to(playhead, {
        offset: 0,
        onUpdate() {
          seamlessLoop.time(wrapTime(playhead.offset));
        },
        duration: 0.5,
        ease: "power3",
        paused: true,
      }),
      trigger = ScrollTrigger.create({
        start: 0,
        onUpdate(self) {
          let scroll = self.scroll();
          if (scroll > self.end - 1) {
            wrap(1, 1);
          } else if (scroll < 1 && self.direction < 0) {
            wrap(-1, self.end - 1);
          } else {
            scrub.vars.offset =
              (iteration + self.progress) * seamlessLoop.duration();
            scrub.invalidate().restart();
          }
        },
        end: "+=3000",
        pin: "#picswrap",
      }),
      progressToScroll = (progress) =>
        gsap.utils.clamp(
          1,
          trigger.end - 1,
          gsap.utils.wrap(0, 1, progress) * trigger.end
        ),
      wrap = (iterationDelta, scrollTo) => {
        iteration += iterationDelta;
        trigger.scroll(scrollTo);
        trigger.update();
      };

    function scrollToOffset(offset) {
      let snappedTime = snapTime(offset),
        progress =
          (snappedTime - seamlessLoop.duration() * iteration) /
          seamlessLoop.duration(),
        scroll = progressToScroll(progress);
      if (progress >= 1 || progress < 0) {
        return wrap(Math.floor(progress), scroll);
      }
      trigger.scroll(scroll);
    }

    Rbtn.current?.addEventListener("click", () =>
      scrollToOffset(scrub.vars.offset + spacing)
    );
    Lbtn.current?.addEventListener("click", () =>
      scrollToOffset(scrub.vars.offset - spacing)
    );

    function buildSeamlessLoop(items, spacing, animateFunc) {
      let rawSequence = gsap.timeline({ paused: true }),
        seamlessLoop = gsap.timeline({
          paused: true,
          repeat: -1,
          onRepeat() {
            this._time === this._dur && (this._tTime += this._dur - 0.01);
          },
          onReverseComplete() {
            this.totalTime(this.rawTime() + this.duration() * 100);
          },
        }),
        cycleDuration = spacing * items.length,
        dur;

      items
        .concat(items)
        .concat(items)
        .forEach((item, i) => {
          let anim = animateFunc(items[i % items.length]);
          rawSequence.add(anim, i * spacing);
          dur || (dur = anim.duration());
        });

      seamlessLoop.fromTo(
        rawSequence,
        {
          time: cycleDuration + dur / 2,
        },
        {
          time: "+=" + cycleDuration,
          duration: cycleDuration,
          ease: "none",
        }
      );
      return seamlessLoop;
    }

    // below is the dragging functionality (mobile-friendly too)...
    // Draggable.create(".drag-proxy", {
    //   type: "x",
    //   trigger: ".cards",
    //   onPress() {
    //     this.startOffset = scrub.vars.offset;
    //   },
    //   onDrag() {
    //     scrub.vars.offset = this.startOffset + (this.startX - this.x) * 0.001;
    //     scrub.invalidate().restart(); // same thing as we do in the ScrollTrigger's onUpdate
    //   },
    //   onDragEnd() {
    //     scrollToOffset(scrub.vars.offset);
    //   },
    // });
  });

  return (
    <>
      <Foodimg id="picswrap">
        <ul className="cards">
          {imageLink.map((link, i) => (
            <li
              key={i}
              className="mainPic"
              style={{
                backgroundImage: `url(https://youbine.github.io/almightyTaco/images/${link})`,
              }}
            ></li>
          ))}
        </ul>
      </Foodimg>

      <div
        style={{
          position: "absolute",
          left: "8%",
          bottom: 0,
          height: "20%",
          width: "50%",
        }}
      >
        <Button
          className="leftBtn"
          style={{
            height: "40%",
            width: "7%",
            marginRight: "5%",
            boxShadow: "none",
          }}
          ref={Lbtn}
        >
          l
        </Button>
        <Button
          ref={Rbtn}
          className="rightBtn"
          style={{ height: "40%", width: "7%", boxShadow: "none" }}
        >
          r
        </Button>
      </div>
      {/* <div
        className="drag-proxy"
        style={{ visibility: "hidden", position: "absolute" }}
      ></div> */}
    </>
  );
}
export default FoodPic;
