import React, { useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import styled from "styled-components";
import Select from "react-select";
import FoodPic from "../components/FoodPic";

export const BodyBg = styled.div`
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  background-color: ${(props) => props.theme.yellow};
  overflow: hidden;
  &::-webkit-scrollbar {
    display: none;
  }
`;
export const Wrap = styled.div`
  width: 95%;
  height: 85%;
  margin: auto;
  background-color: white;
  border: 5px dotted black;
  text-align: center;
  position: relative;
  display: flex;
`;
export const StoreName = styled.h2`
  font-family: "Bangers", cursive;
  position: absolute;
  color: red;
  left: 10%;
  top: -8%;
  font-size: 6vmax;
  -webkit-text-stroke: black 0.02em;
  text-shadow: -0.04em 0.03em 0 black, -0.08em 0.05em 0 black;
`;
const StoreForm = styled.div`
  width: 40%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;
export const Selection = styled(Select)`
  font-size: 1.5vmax;
  margin-bottom: 10%;
  text-transform: uppercase;
`;

export const Button = styled.button`
  cursor: pointer;
  text-transform: uppercase;
  font-size: 2vmax;
  color: #fe5207;
  font-weight: bold;
  width: 70%;
  line-height: 1.7;
  border-radius: 5rem;
  border: none;
  background-color: ${(props) => props.theme.yellow};
  box-shadow: 0.25em 0.2em 0 0.1em #fe5207;
`;
function Store() {
  const [store, setStore] = useState("");
  const selectRef = useRef();
  const navigate = useNavigate();

  const goToStore = () => {
    store === "" ? selectRef.current.focus() : navigate(`${store}?menu=main`);
  };

  const handleChange = (event) => {
    setStore(event.value);
  };
  const stores = [
    { value: "korea", label: "Korea" },
    { value: "mexico", label: "Mexico" },
    { value: "usa", label: "Usa" },
  ];

  return (
    <BodyBg>
      <Wrap>
        <StoreName>almighty Taco</StoreName>
        <FoodPic />
        <StoreForm>
          <Selection
            placeholder="Please pick store to Order"
            options={stores}
            ref={selectRef}
            onChange={handleChange}
          />
          <Button onClick={goToStore}>Go grab taco</Button>
        </StoreForm>
      </Wrap>
    </BodyBg>
  );
}
export default Store;
