import styled from "styled-components";
import { useLocation, useSearchParams } from "react-router-dom";
import { motion } from "framer-motion";
import Quantity from "./Quantity";
import { useEffect, useRef, useState } from "react";
import { device } from "../devices";

const MenuWrap = styled(motion.div)`
  user-select: none;
  overflow-x: hidden;
  white-space: pre;
  width: 100%;
  height: 93%;
  position: relative;
  right: 0;
  top: 8%;
  &::-webkit-scrollbar {
    display: none;
  }
  @media ${device.laptop} {
    top: -3rem;
    width: 100%;
    height: 100%;
  }
`;
const MenuInner = styled(motion.div)`
  display: flex;
  align-items: center;
  cursor: grab;
  position: absolute;
  width: auto;
  height: 100%;
  @media ${device.laptop} {
    width: 100%;
    height: auto;
    display: grid;
    align-items: center;
    justify-items: center;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }
`;

export const List = styled(motion.div)`
  cursor: pointer;
  border: 2px solid black;
  background-color: white;
  padding: 1rem;
  position: relative;
  width: 18vw;
  min-width: 16rem;
  height: 70%;
  max-height: 25rem;
  margin: 2vw;
  box-shadow: 0.7rem 0.7rem rgb(100 100 550 / 30%);
  transition: 0.2s ease;
  &:hover {
    box-shadow: 0.2rem 0.2rem rgb(100 100 550 / 30%);
  }
  @media ${device.tablet} {
    min-width: 10rem;
    width: 25vw;
    max-height: 19rem;
    min-height: 16rem;
    margin-top: 7rem;
    height: 60%;
  }
`;
export const Name = styled(motion.h1)`
  position: absolute;
  top: -2rem;
  left: 50%;
  transform: skewX(-10deg) rotate(-2deg) translateX(-50%);
  background-color: black;
  width: fit-content;
  padding: 0.8rem;
  text-transform: uppercase;
  font-size: 1.7rem;
  font-weight: bold;
  color: white;
  @media ${device.tablet} {
    font-size: 1rem;
  }
`;
const Img = styled.img`
  margin: 15% 0;
  object-fit: contain;
  height: 37%;
  width: 100%;
  /* @media ${device.mobileL} {
    width: 30vw;
    height: 30vw;
  } */
`;
const Price = styled.p`
  position: absolute;
  right: 0;
  top: 7%;
  padding: 0.3rem;
  width: 6rem;
  background: rgba(107, 203, 16, 0.7);
  color: white;
  font-size: 1.2rem;
  span {
    opacity: 1;
  }
  &::before {
    content: "";
    display: block;
    top: 0;
    left: -2rem;
    position: absolute;
    border-bottom: 0.8rem solid transparent;
    border-top: 1rem solid transparent;
    border-left: 1rem solid transparent;
    border-right: 1rem solid rgba(107, 203, 16, 0.7);
  }
  @media ${device.tablet} {
    font-size: 0.8rem;
    width: 4rem;
    &::before {
      left: -1.39rem;
      border-bottom: 0.7rem solid transparent;
      border-top: 0.7rem solid transparent;
      border-left: 0.7rem solid transparent;
      border-right: 0.7rem solid rgba(107, 203, 16, 0.7);
    }
  }
`;

function Food({ data }) {
  const storeLocation = useLocation().pathname;
  const [searchParams] = useSearchParams();
  const menu = searchParams.get("menu");
  const scrollRef = useRef(0);
  const cardWidth = useRef(0);
  const [scrollWidth, setScrollWidth] = useState(0);
  const [windowWidth, setWindowWidth] = useState(0);

  useEffect(() => {
    setScrollWidth(cardWidth.current.offsetWidth);
    setWindowWidth(scrollRef.current.offsetWidth);
    const setSize = () => {
      setScrollWidth(cardWidth.current.offsetWidth);
      setWindowWidth(scrollRef.current.offsetWidth);
    };
    window.addEventListener("resize", setSize);
    return () => {
      window.addEventListener("resize", setSize);
    };
  }, [cardWidth, scrollRef]);

  return (
    <MenuWrap ref={scrollRef}>
      <MenuInner
        ref={cardWidth}
        drag="x"
        initial={{ x: 0 }}
        dragConstraints={{
          left: -scrollWidth + windowWidth,
          right: 0,
        }}
        dragTransition={{ bounceStiffness: 200, bounceDamping: 15 }}
        animate={{ x: [window.innerWidth, 0] }}
        transition={{ type: "spring", duration: 1 }}
      >
        {data.map((food, index) => (
          <List
            whileHover={{ scale: 1.1, transition: "0.2s" }}
            whileTap={{ scale: 1.1, transition: "0.2s" }}
            key={index}
          >
            <Name>{food.name}</Name>
            <Img src={food.image} />
            <Price>
              {storeLocation.includes("korea") ? (
                <span>{food.price} </span>
              ) : (
                <span>$ {food.price}</span>
              )}
            </Price>
            <Quantity
              index={index}
              food={food}
              location={storeLocation}
              menu={menu}
            />
          </List>
        ))}
      </MenuInner>
    </MenuWrap>
  );
}
export default Food;
